@import "../../node_modules/bootstrap/scss/bootstrap-reboot";
@import "../../node_modules/bootstrap/scss/grid";
@import "../../node_modules/bootstrap/scss/utilities/flex";
@import "../../node_modules/bootstrap/scss/dropdown";

@import "../fonts/fontello-b088dac0/css/fontello.css";
@import "components/variables";
@import "components/mixins";
@import "components/header";
@import "components/media";

@font-face {
	font-family: 'philosopher';
	src: url('../fonts/Philosopher/Philosopher-Regular.ttf');
	font-weight: normal;
	font-style: normal;
	}
@font-face {
	font-family: 'philosopher';
	src: url('../fonts/Philosopher/Philosopher-Bold.ttf');
	font-weight: bold;
	font-style: normal;
	}
@font-face {
	font-family: 'philosopher';
	src: url('../fonts/Philosopher/Philosopher-Italic.ttf');
	font-weight: normal;
	font-style: italic;
	}
@font-face {
	font-family: 'philosopher';
	src: url('../fonts/Philosopher/Philosopher-BoldItalic.ttf');
	font-weight: bold;
	font-style: italic;
	}
@font-face {
	font-family: 'philosopher';
	src: url('../fonts/Philosopher/Philosopher-BoldItalic.ttf');
	font-weight: bold;
	font-style: italic;
	}
@font-face {
	font-family: 'righteous';
	src: url('../fonts/Righteous-Regular.ttf');
	font-weight: normal;
	font-style: normal;
	}
%more_btn {
	transition: all .25s;
	color: $accent-color;
	font-size: 18px;
	text-transform: uppercase;
	border: 3px solid $accent-color;
	width: $width-read-more;
	height: $height-read-more;
	text-align: center;
	line-height: 50px;
	font-family: $font-family-righteous;
	background: transparent;
}	
a {
	text-decoration: none;
	&:hover {
		text-decoration: none;
	}
}
main {
	.col {
		min-width: 320px;
		padding-top: 30px;
	}
	.article {
		display: block;
		position: relative;
		height: 230px;
		clip-path: polygon(0px 0px, 100% 0px, 100% 97%, 52px 97%, 45px 100%, 38px 97%, 0px 97%);
		&:before {
			opacity: 0;
			position: absolute;
			display: block;
			content: '';
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;
			background-color: rgba($black-bg, 0.7);
			transition: .4s;
		}
		&:after {
			opacity: 0;
			transition: .25s;
			transition-delay: .1s;
			transform: scale(0);
			position: absolute;
			content: 'Read more';
			display: inline-block;	
			left: 50%;
			top: 50%;
			margin-left: -$width-read-more/2;
			margin-top: -$height-read-more/2;
			@extend %more_btn;
			&:hover {
				background-color: $accent-color;
				color: $white;
			}
		}
		&:hover::before {
			opacity: 1;
		}
		&:hover::after {
			opacity: 1;
			transform: scale(1);
		}
		img {
			width: 100%;
			height: auto;
		}
	}
	.desc {
		color: $black-text;
		padding: 20px;
		padding-right: 10px;
		font-family: $font-family-philosopher;
		&:after {
			position: absolute;
			content: '';
			display: block;
			background: $accent-color;
			height: 5px;
			left: 4%;
			width: 92%;
			bottom: 0;
		}
		span {
			color: $grey-light;
			font-size: 12px;
			&:first-child {
				margin-right: 15px;
			}
		}
		h3 {
			font-size: 22px;
			font-weight: bold;
			margin: 15px auto;
		}
		p {
			font-size: 14px;
		}
	}
}

@for $i from 1 through 8 {
	.article.item-#{$i} { 
		background: url('../img/media/img#{$i}.jpg') no-repeat top left /cover; 
	}
}
.load {
	display: block;	
	margin: 30px auto;
	@extend %more_btn;
	&:hover {
		background-color: $accent-color;
		color: $white;
	}
}
