/*header*/
header {
	background-color: $black-bg;
	min-height: 65px;
	color: $white;
	font-family: $font-family-righteous;
	text-transform: uppercase;
	line-height: auto;
	h1 {
		font-size: 22px;
		padding: 15px;
		margin: 0;
		i {
			color: $accent-color;
			padding: 0 15px;
		}
	}
	%typo_header {
		padding: 20px;
		color: $grey-light;
		text-align: center;
	}
	%shadow {
		box-shadow: 4px 0px 7px 0 rgba(0, 0, 0, 0.2);
	}
	.menu {
		width: 210px;
		height: inherit;
		.dropdown {
			max-width: 90px;
			transition: all .2s;
			height: 65px;
			@extend %shadow;
			&:hover {
				background-color: $black-bg-hover;
				box-shadow: none;
				>button {
					color: $white;
				}
			}
		}
		.dropdown-toggle {
			color: $grey-light;
			@extend %typo_header;
			border: none;
			width: 100%;
			height: 100%;
			background-color: transparent;
			text-transform: uppercase;
			outline: none;
			font-size: 12px;
			&::after {
				display: none;
			}
		}
		.dropdown-menu {
			@extend %shadow;
			padding: 7px 15px;
			.dropdown-item {
				outline: none;
				height: 40px;
				font-size: 10px;
				text-transform: uppercase;
				padding: 0;
				position: relative;
				transition: all .15s;
				cursor: pointer;
				&:after {
					content: '';
					position: absolute;
					background-color: #e9e9e9;
					width: 100%;
					height: 1px;
					bottom: 0;
					left: 0;
				}
				&:last-child:after {
					display: none;
				}
				&:hover {
					background-color: lighten(rgba($grey-light, .2), 10%);
				}
			}	
		}
	}
	.search, .user {
		@extend %typo_header;
		max-width: 60px;
		font-size: 16px;
		height: 65px;
		transition: all .25s;
		@extend %shadow;
		&:hover {
			background-color: $black-bg-hover;
			color: $white;
			box-shadow: none;
		}	
	}
}
